.media{
    margin-left: 1em;
    position: absolute;
    right: .2em;
    top: 0px;
    z-index: 1;
    display: flex;
}

.fa {
    padding: .5em;
    font-size: 1.2em;
    width: 2em;
    text-align: center;
    text-decoration: none;
    margin: 5px 5px;
}

.fa:hover {
    opacity: 0.7;
}

.fa-facebook {
    background: #3B5998;
    color: white;
}

.fa-twitter {
    background: #55ACEE;
    color: white;
}

.fa-linkedin {
    background: #007bb5;
    color: white;
}

.fa-github {
    background: #211F1F;
    color: white;
}