.line{
    text-align: unset;
    display: flex;
    justify-content: center;
}

.line div{
    background-color: transparent;
    border: none;
    font-family: 'Montserrat', sans-serif;
    font-size: 1em;
    margin-top: .85em;
    padding: .21em 1.5em 0 1.5em;
}

.default:hover, .purple:hover{
    cursor: pointer;
    color: #a102a1;
}

.default:active{
    background-color: #deecf9;
}

.default:focus{
    outline: none;
}

.purple hr{
    width: inherit;
    border: .08em solid purple;
    border-radius: .1em;
    margin: .3em 0 0 0;
}

.purple{
    color:purple;
}

.default, .purple{
    flex-direction: column;
    direction: initial;
}