.App {
  font-family: 'Montserrat', sans-serif;
  min-height: 100vh;
  background-color: #deecf9;
}

.App-header {
  min-width: 100%;
  min-height: 3em;
  display: flex;
  flex-direction: column;
  font-size: calc(5px + 2vmin);
  color: #424242;
}

.App-content {
  height: 92vh;
  background-image: linear-gradient( #deecf9 0.5vh, #eff6fc 90vh);
  overflow: auto;
}

h1{
  color:purple;
}

