.section{
    display: flex;
    flex-direction: row;
    height: 100%;
}

.left{
    height: 100%;
    width: 70%;
}
.right{
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    width: 30%;
    margin: 2em;
    color: #424242;
}

.right button{
    display: flex;
    justify-content: space-between;
    background-color: #eff6fc;
    color: #424242;
    border: none;
    font-family: 'Montserrat', sans-serif;
    font-size: 1em;
    padding: 1.5em 2em;
    margin: 1em;
    box-shadow: 0.5em 0.5em 1em #cee3f6;
}

.right button:hover{
   cursor: pointer;
   color: #a102a1;
}

.right button:active{
   background-color: #deecf9;
}

.right button:focus{
   outline: none;
}

.icon{
    margin-left: 1.5em;
}

.main-icon{
    color: purple;
    height: 4em;
    width: 4em;
    margin: 0.5em;
}

a:link{
    text-decoration: none;
}